import React, { useState, useEffect } from 'react';
import { Button, Form, Container, Header } from 'semantic-ui-react';
import { Link } from 'gatsby';
import axios from 'axios';
import "../css/evalForm.css";
import { stripBasename } from 'history/PathUtils';
import { createBrowserHistory } from "history";

function signUp() {
    const [firstname, setFirst] = useState('');
    const [lastname, setLast] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const history = createBrowserHistory();

    const directToMenu = () => {
        history.push('/menu');
        window.location.reload();
    }

    const handleSubmit = (e) => {
        directToMenu();
    }

    return (
        <Container fluid className="container">
            <Header as="h2">Sign Up</Header>

            <Form className="form">
                <Form.Field>
                    <label class="formlabels">Please enter your first name.</label>
                    <input type="text" id="firstname" name="firstname" size="50" onChange={(e) => setFirst(e.target.value)}/>
                </Form.Field>
                <div></div><br/>

                <Form.Field>
                    <label class="formlabels">Please enter your last name.</label>
                    <input type="text" id="lastname" name="lastname" size="50" onChange={(e) => setLast(e.target.value)}/>
                </Form.Field>
                <div></div><br />

                <Form.Field>
                    <label class="formlabels">Please enter your email.</label>
                    <input type="email" id="email" name="email" size="50" onChange={(e) => setEmail(e.target.value)}/>
                </Form.Field>
                <div></div><br />

                <Form.Field>
                    <label class="formlabels">Please enter a password.</label>
                    <input type="text" id="password" name="password" size="50" onChange={(e) => setPassword(e.target.value)}/>
                </Form.Field>
                <div></div><br />

                <Button type="submit" onClick={handleSubmit}>Submit</Button>
            </Form>
        </Container>
    )
}

export default signUp;